import Header from "./components/layout/Header";
import Footer from "./components/layout/Footer";
import Link from "next/link";
import Image from "next/image";
import {
  ArrowRight,
  Building2,
  Car,
  Newspaper,
  Search,
  MapPin,
  Gauge,
  Calendar,
  Fuel,
  Star,
  Phone,
} from "lucide-react";
import { CARS, SHOWROOMS, NEWS, BRANDS } from "@/lib/data";

export default function Home() {
  return (
    <div className="min-h-screen bg-white">
      <Header />

      {/* Hero Section - Simple et Clair */}
      <section className="relative bg-[#1a1a2e] text-white py-20">
        <div className="absolute inset-0 opacity-30">
          <Image
            src="https://images.unsplash.com/photo-1492144534655-ae79c964c9d7?w=1920"
            alt="Hero"
            fill
            className="object-cover"
            priority
          />
        </div>

        <div className="relative max-w-7xl mx-auto px-4 text-center">
          <h1 className="text-5xl md:text-6xl font-bold mb-6">
            TROUVEZ VOTRE VOITURE
          </h1>
          <p className="text-xl text-gray-300 mb-8 max-w-2xl mx-auto">
            Prix, occasions et concessionnaires en Algérie
          </p>

          {/* Search Bar */}
          <div className="max-w-3xl mx-auto bg-white rounded-lg shadow-xl p-2 flex gap-2">
            <input
              type="text"
              placeholder="Rechercher une voiture..."
              className="flex-1 px-4 py-3 text-gray-700 focus:outline-none"
            />
            <button className="px-8 py-3 bg-[#D32F2F] text-white rounded font-semibold hover:bg-[#B71C1C]">
              Rechercher
            </button>
          </div>
        </div>
      </section>

      {/* Marques - Grille Simple */}
      <section className="py-16 bg-gray-50">
        <div className="max-w-7xl mx-auto px-4">
          <h2 className="text-3xl font-bold text-gray-900 mb-8">Marques Populaires</h2>

          <div className="grid grid-cols-3 sm:grid-cols-4 md:grid-cols-5 lg:grid-cols-7 gap-4">
            {BRANDS.map((brand) => (
              <Link
                key={brand}
                href={`/marques/${brand.toLowerCase()}`}
                className="bg-white p-6 rounded-lg text-center hover:shadow-lg transition-shadow border border-gray-200"
              >
                <div className="w-16 h-16 mx-auto mb-3 bg-gray-100 rounded-lg flex items-center justify-center">
                  <span className="text-2xl font-bold text-gray-400">{brand[0]}</span>
                </div>
                <p className="text-sm font-semibold text-gray-700">{brand}</p>
              </Link>
            ))}
          </div>
        </div>
      </section>

      {/* Concessionnaires */}
      <section className="py-16 bg-white">
        <div className="max-w-7xl mx-auto px-4">
          <div className="flex justify-between items-center mb-8">
            <h2 className="text-3xl font-bold text-gray-900">Concessionnaires</h2>
            <Link href="/concessionnaires" className="text-[#D32F2F] font-semibold flex items-center gap-2 hover:underline">
              Voir tous <ArrowRight className="w-5 h-5" />
            </Link>
          </div>

          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
            {SHOWROOMS.map((showroom) => (
              <Link
                key={showroom.id}
                href={`/concessionnaires/${showroom.id}`}
                className="bg-white border border-gray-200 rounded-lg overflow-hidden hover:shadow-xl transition-shadow"
              >
                <div className="relative h-48">
                  <Image src={showroom.image} alt={showroom.name} fill className="object-cover" />
                  {showroom.rating && (
                    <div className="absolute top-3 right-3 bg-white px-2 py-1 rounded-full flex items-center gap-1">
                      <Star className="w-4 h-4 text-yellow-500 fill-yellow-500" />
                      <span className="text-sm font-bold">{showroom.rating}</span>
                    </div>
                  )}
                </div>
                <div className="p-4">
                  <h3 className="font-bold text-lg text-gray-900 mb-2">{showroom.name}</h3>
                  <p className="text-sm text-gray-600 flex items-center gap-2 mb-1">
                    <MapPin className="w-4 h-4" /> {showroom.city}
                  </p>
                  <p className="text-sm text-gray-600 flex items-center gap-2">
                    <Car className="w-4 h-4" /> {showroom.cars} voitures
                  </p>
                </div>
              </Link>
            ))}
          </div>
        </div>
      </section>

      {/* Voitures d'Occasion */}
      <section className="py-16 bg-gray-50">
        <div className="max-w-7xl mx-auto px-4">
          <div className="flex justify-between items-center mb-8">
            <h2 className="text-3xl font-bold text-gray-900">Voitures d'Occasion</h2>
            <Link href="/occasions" className="text-[#D32F2F] font-semibold flex items-center gap-2 hover:underline">
              Voir toutes <ArrowRight className="w-5 h-5" />
            </Link>
          </div>

          <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-4 gap-6">
            {CARS.map((car) => (
              <Link
                key={car.id}
                href={`/occasions/${car.id}`}
                className="bg-white border border-gray-200 rounded-lg overflow-hidden hover:shadow-xl transition-shadow"
              >
                <div className="relative h-48">
                  <Image src={car.image} alt={`${car.brand} ${car.model}`} fill className="object-cover" />
                  {car.isNew && (
                    <span className="absolute top-3 left-3 bg-green-500 text-white px-3 py-1 rounded-full text-xs font-bold">
                      NEUVE
                    </span>
                  )}
                </div>
                <div className="p-4">
                  <h3 className="font-bold text-gray-900">{car.brand} {car.model}</h3>
                  <p className="text-2xl font-bold text-[#D32F2F] mt-1">{car.price}</p>
                  <div className="mt-3 space-y-1 text-sm text-gray-600">
                    <p className="flex items-center gap-2"><MapPin className="w-4 h-4" /> {car.city}</p>
                    <p className="flex items-center gap-2"><Gauge className="w-4 h-4" /> {car.km}</p>
                    <p className="flex items-center gap-2"><Calendar className="w-4 h-4" /> {car.year}</p>
                  </div>
                </div>
              </Link>
            ))}
          </div>
        </div>
      </section>

      {/* Actualités */}
      <section className="py-16 bg-white">
        <div className="max-w-7xl mx-auto px-4">
          <div className="flex justify-between items-center mb-8">
            <h2 className="text-3xl font-bold text-gray-900">Actualités</h2>
            <Link href="/actualites" className="text-[#D32F2F] font-semibold flex items-center gap-2 hover:underline">
              Voir toutes <ArrowRight className="w-5 h-5" />
            </Link>
          </div>

          <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-4 gap-6">
            {NEWS.slice(0, 4).map((article) => (
              <Link
                key={article.id}
                href={`/actualites/${article.slug}`}
                className="bg-white border border-gray-200 rounded-lg overflow-hidden hover:shadow-xl transition-shadow"
              >
                <div className="relative h-48">
                  <Image src={article.image} alt={article.title} fill className="object-cover" />
                </div>
                <div className="p-4">
                  <span className="text-xs font-semibold text-[#D32F2F] uppercase">{article.category}</span>
                  <h3 className="font-bold text-gray-900 mt-2 line-clamp-2">{article.title}</h3>
                  <p className="text-sm text-gray-600 mt-2">{article.date}</p>
                </div>
              </Link>
            ))}
          </div>
        </div>
      </section>

      {/* CTA */}
      <section className="py-20 bg-[#D32F2F] text-white">
        <div className="max-w-4xl mx-auto px-4 text-center">
          <h2 className="text-4xl font-bold mb-4">Vendez votre voiture</h2>
          <p className="text-lg mb-8">Publiez votre annonce gratuitement</p>
          <div className="flex flex-col sm:flex-row gap-4 justify-center">
            <Link href="/publier-annonce" className="px-8 py-4 bg-white text-[#D32F2F] rounded-lg font-bold hover:bg-gray-100">
              Publier une annonce
            </Link>
            <Link href="/inscription" className="px-8 py-4 border-2 border-white rounded-lg font-bold hover:bg-white hover:text-[#D32F2F]">
              Créer un compte
            </Link>
          </div>
        </div>
      </section>

      <Footer />
    </div>
  );
}
